.banner {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  height: 15rem;

  background-image: url(../../assets/images/City-night.jpg);
  background-position: center;
  background-size: cover;

  &__title {
    color: white;
    font-weight: bold;
  }
}
